        (<span style="cursor:pointer; font-weight: 700; color: gray;"><span class = "hovers"  OnClick="window.open('https://astlyi.s3.ap-northeast-2.amazonaws.com/2024/%5B123%5DCoCoStyle.pdf', '_blank')">Elsevier SoftwareX</span></span>)
